// 引入定位
import { addressdata } from '../../common/list.js';
// 引入SDK核心类
var QQMapWX = require('../../common/qqmap-wx-jssdk.js');
var qqmapsdk = new QQMapWX({
	key: 'M5IBZ-FPCHS-NM6OI-6CY27-IN2J7-H7FJG'
});
export default {
	name: 'citying',
	data() {
		return {
			citynone: true,
			address: '',
			citydata: [], //搜索的城市
			keywoeds: '',
			pageroute: '', //从哪个页面进来的路由
			city: [
				{
					name: '新校区'
				},
				{
					name: '北校区'
				},
				{
					name: '滨江校区'
				},
				{
					name: '体育馆'
				},
				{
					name: '南校区'
				}
			],
			citydata1:[
				{
					name: '新校区'
				},
				{
					name: '北校区'
				},
				{
					name: '滨江校区'
				},
				{
					name: '体育馆'
				}
			]
		};
	},
	methods:{
		//搜索触发
		searchCity(e){
			console.log(e)
			this.citynone = false
		},
		//点击取消，关闭搜索
		canCal(){
			this.citynone = true
		},
		//点击定位到城市名称
		clickCity(){
			// console.log(this.address)
			let cityion = this.address
			this.backRoutes(cityion)
		},
		//取到热门景点
		hotCity(city){
			// console.log(city)
			this.backRoutes(city)
		},
		
		//跳转到攻略页面
		backRoutes(cityion){
			// console.log(cityion)
			//vuex传值
			this.$store.commit('citymuta',cityion)
			uni.navigateBack({
				delta:1
			})
		},
		//实时搜索城市
		searchInput(e){
			// console.log(e.detail.value)
			qqmapsdk.getSuggestion({
				keyword:e.detail.value ,
				// region:"湘潭市",
				success:(res)=>{
					console.log(res)
				},
				fail:(err)=>{
					console.log(err)
				}
			})
		},
		//定位当前城市
		addRess(){
			addressdata()
			.then((res)=>{
				console.log(res)
				this.address = res.result.address_reference.landmark_l2.title
			})
			.catch((err)=>{
				console.log("用户拒绝定位")
				this.address = '新校区'
			})
		}
	},
	mounted() {
		this.addRess()
	}
};
